.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
/* Custom Scrollbar */
::-webkit-scrollbar {
    width: 6px;           /* Small width */
}

::-webkit-scrollbar-track {
    background: #1e1e1e;  /* Track color (optional) */
}

::-webkit-scrollbar-thumb {
    background: #b6ff40;    /* Scroll bar color */
    border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
    background: linear-gradient(90deg, #9ACD03, #AACC04);;  /* Slightly lighter green on hover */
}
